<template>
  <div class="row">
   <div class="col-md-12">
      <div class="row">
        <div class="col-md-4">
          <fg-input
            @changeValue="globalSearch"
            placeholder="Find user"
            :value="searchValue"
          />
        </div>
        <div class="col-md-4">
          <router-link class="btn btn-success" to="/users/role/add">
            Add new role
          </router-link>
        </div>
      </div>
      <div class="card" v-if="users.length !== 0">
        <paper-table 
          :title="table.title" 
          :sub-title="table.subTitle" 
          :data="table.data" 
          :columns="table.columns"
          :actions="table.actions"
          @clickDelete="() => {}"
        />
        <button
          class="btn btn-primary pull-right newUserButton"
          @click="showMoreUsers"
          v-if="moreUsers || count > 10"
        >
          Show more
        </button>
      </div>
    </div>
  </div>
</template> 

<script>
  import { mapGetters, mapActions, mapState } from 'vuex'
  import { tableColumns, tableActions } from './TableMock.js'

  export default {
    components: {
      PaperTable: () => import('ui/PaperTable.vue')
    },
    data: () => ({
      tableActions,
      tableColumns,
      table: {
        title: 'Users list',
        subTitle: 'Manage and check users',
        columns: [...tableColumns],
        actions: [...tableActions],
        data: []
      }
    }),
    mounted() {
      this.fetchUsers()
    },
    watch: {
      users (value) {
        if (value) {
          this.table.data = [
            ...value
          ]
        }
      }
    },
    computed: {
      ...mapGetters({
        users: 'getUsers',
        moreUsers: 'moreUsers'
      }),
      ...mapState({
        searchValue: ({ users: { searchValue } }) => searchValue,
        count: ({ users: { count } }) => count
      })
    },
    methods: {
      globalSearch() {

      },
      handleDeleteUser(value) {
        const id = value
        this.removeUser({ id })
      },
      ...mapActions([
        'fetchUsers',
        'showMoreUsers',
        'userSearch'
      ])
    }
  }
</script>

<style lang="css" scoped>
  .newUserButton {
    margin: 10px 0px;
    display: block;
    width: 100%;
  }

  .alert {
    margin: 0px 0px 10px;
  }
</style> 



